import React, {useState} from 'react';

import Rating from './Rating';

const Song = ({song}) => {
    const {id, title, artist,url,votes,rating} = song;

    const [hovered, setHovered] = useState(false);

    const styleHover = {textDecoration: 'underline'};
    const styleNormal = {textDecoration: 'none'};

    return (
      <div>
        <h4 style={{ fontSize: "20px", marginBottom: "3px", lineHeight: 1.3 }}>
          <a
            href={url}
            target="_blank"
            rel="noopener noreferrer"
            style={hovered ? styleHover : styleNormal}
            onMouseEnter={() => setHovered(true)}
            onMouseOut={() => setHovered(false)}
          >
            {artist} - {title}
          </a>
        </h4>
        <Rating id={id} rating={rating} votes={votes} />
      </div>
    );
}

export default Song;
