import React, {Fragment, useContext, useEffect} from 'react';

import Song from './Song';
import songsContext from '../context/songsContext';

const SongList = (props) => {
    // const songs = [{
    //     "id": 1,
    //     "title": "Savage",
    //     "artist": "Megan Thee Stallion",
    //     url: "https://www.youtube.com/watch?v=EOxj2ROIxok",
    //     "votes": 0,
    //     "rating": 0.0
    // },
    // {
    //     "id": 2,
    //     "title": "Rain On Me",
    //     "artist": "Lady Gaga, Ariana Grande",
    //     url: "https://www.youtube.com/watch?v=AoAm4om0wTs",
    //     "votes": 2,
    //     "rating": 5.0
    // },
    // {
    //     "id": 3,
    //     "title": "Do It",
    //     "artist": "Chloe x Halle",
    //     url: "https://www.youtube.com/watch?v=a9HIaGcBocc",
    //     "votes": 0,
    //     "rating": 0.0
    // }];

    const {songs, fetchSongs} = useContext(songsContext);

    useEffect( () => {
        fetchSongs();
        // eslint-disable-next-line
    }, []);

    // console.log("songs - ", songs);
    if ( ! songs ) {
        return (
            <div>No songs were found.</div>
        );
    }

    // const songsStyle = {
    //     display: 'grid',
    //     gridTemplateColumns: 'repeat(2, 0.5fr)',
    //     gridGap: '1rem'
    // };

    return (
      <Fragment>
        <h1
          style={{
            marginTop: "20px",
            marginBottom: "20px",
            textAlign: "center",
          }}
        >
          Top Songs On Youtube
        </h1>
        <div className="grid-2">
          {songs.map((song) => (
            <Song key={song.id} song={song} />
          ))}
        </div>
      </Fragment>
    );
}

export default SongList;
