import React, {useState, useContext} from 'react';

import RatingStar from './RatingStar';
import SongsContext from '../context/songsContext';

function Rating({id, rating, votes}) {
    const [stars, setStars] = useState([false, false, false, false, false]);
    const [ratedStars, setRatedStars] = useState(0);

    const {rateSong} = useContext(SongsContext);

    const hoverRatingStar = (indexHovered) => {
      // console.log("hover star ", indexHovered);

      if (ratedStars > 0) {
        return;
      }

      setStars(stars.map((star, idx) => (indexHovered >= idx ? true : false)));
    };

    const handleMouseOut = (e) => {
      // console.log("Mouse out rating");

      if (ratedStars > 0) {
        return;
      }

      setStars([false, false, false, false, false]);
    };

    const clickRatingStar = (index) => {
      // console.log("click star ", index);

      if (ratedStars > 0) {
        return alert('You already rated this song! You can rate it again after 15 seconds!');
      }

      setRatedStars(index + 1);

      rateSong(id, index+1);

      setTimeout(() => resetRatedStars(), 15000);
    };

    const resetRatedStars = () => {
      // console.log("reset stars...");
      setRatedStars(0);
    };

    const formattedRating = new Intl.NumberFormat("en", {maximumFractionDigits: 1}).format(rating);
    // console.log('Formatted rating: ', formattedRating);

    return (
      <div>
        <span>
          Rating {formattedRating} from {votes} votes
        </span>
        <span
          style={{
            paddingLeft: "10px",
            paddingRight: "10px",
            fontWeight: "bold",
          }}
        >
          |
        </span>
        <span onMouseLeave={handleMouseOut}>
          <span
            style={{
              paddingRight: "10px",
            }}
          >
            Rate
          </span>
          {stars.map((star, index) => (
            <RatingStar
              key={index}
              index={index}
              isOn={star}
              hoverRatingStar={hoverRatingStar}
              clickRatingStar={clickRatingStar}
            />
          ))}
        </span>
      </div>
    );
}

export default Rating;
