import React, {Fragment} from 'react';
import {Link} from 'react-router-dom';

const About = (props) => {
 
    return (
      <Fragment>
        <div className="my-2">
          This Java application creates a REST Service that exposes a few
          endpoints to the clients.
          <p className="my-1">
            On the <Link to="/">Home page</Link> you can see a demonstration of
            using this REST Service in a client application. The client
            application is build using React.
          </p>
          <p>
            In addition, the REST Service provides full support for integrating
            with Prometheus through the Metrics endpoint which provides metrics information
            about the application. <a href="https://prometheus.io/">Prometheus</a> is an open-source
            systems monitoring and alerting toolkit.
          </p>
          <p className="my-1">
            To see and test the exposed endpoints of the REST Service click on{" "}
            <Link to="/test">Test Endpoints</Link>.
          </p>
        </div>
      </Fragment>
    );
}

export default About;
