import React, {Fragment} from 'react';

import SongListTest from './SongListTest';
import SongRatingTest from './SongRatingTest';
import MetricsTest from './MetricsTest';

const TestEndpoints = (props) => {
    return (
      <Fragment>
        <SongListTest />
        <hr className="m-2" />
        <SongRatingTest />
        <hr className="m-2" />
        <MetricsTest />
        <div className="m-2"></div>
      </Fragment>
    );
}

export default TestEndpoints;