import {
  FETCH_SONGS,
  RATE_SONG,
  SET_LOADING,
  SET_ERROR,
  CLEAR_ERROR,
} from "./types";

export default (state, action) => {
  console.log("current state: ", state, "action: ", action);

  switch (action.type) {
    case FETCH_SONGS:
      return { ...state, songs: action.payload, loading: false };
    case RATE_SONG:
      const ratedSong = action.payload;
      const updatedSongs = state.songs.map((song) =>
        song.id === ratedSong.id ? ratedSong : song
      );
      return { ...state, songs: updatedSongs, loading: false };
    case SET_LOADING:
      return { ...state, loading: true };
    case SET_ERROR:
      return { ...state, loading: false, error: action.payload };
    case CLEAR_ERROR:
      return { ...state, error: null };
    default:
      return state;
  }
};
