import React, {useContext, useState} from 'react';

import SongsContext  from '../../context/songsContext';

const SongListTest = (props) => {
    const [result, setResult] = useState(null);

    const {fetchSongs} = useContext(SongsContext);

    const handleTryIt = async (e) => {
        const data = await fetchSongs();
        setResult(JSON.stringify(data, null, 2));
    };

    const handleClear = async (e) => {
        setResult(null);
    };

    // const songsStyle = {
    //     display: 'grid',
    //     gridTemplateColumns: 'repeat(2, 1fr)',
    //     gridGap: '20px'//'0.5rem'
    // };

    return (
      <div className="container">
        <div>
          <h3>Fetch all songs</h3>
          <p>Endpoint: java_sample/v1/songs</p>
          <p>Method: GET</p>
          {/* <p className="list">Parameters: -</p> */}
          <p>Result type: JSON</p>
          <div className="py-1">
            <button onClick={handleTryIt} className="btn btn-primary">
              Try it
            </button>
            {result && (
              <button onClick={handleClear} className="btn btn-light">
                Clear
              </button>
            )}
          </div>
        </div>
        {result && (
          <div>
            <h4 className="py-1">Result</h4>
            <div>{result}</div>
          </div>
        )}
      </div>
    );
};

export default SongListTest;