import React from 'react';

const RatingStar = ({index, isOn, hoverRatingStar, clickRatingStar}) => {

    const handleHover = (e) => {
        hoverRatingStar(index);
    }

    const handleClick = (e) => {
        clickRatingStar(index);
    }

    return (
        <span onMouseEnter={handleHover} onClick={handleClick}>
            <i className={`${isOn ? 'fas' : 'far'} fa-star`}></i>
        </span>
    )
}

export default RatingStar;
