import {useReducer} from 'react';
import axios from 'axios';

import {FETCH_SONGS, RATE_SONG, SET_LOADING, SET_ERROR, CLEAR_ERROR} from './types';

import SongsContext from './songsContext';
import songsReducer from './songsReducer';

import React from 'react';

const NODE_ENV = process.env.NODE_ENV ? process.env.NODE_ENV: 'development';
console.log('NODE_ENV: ', NODE_ENV);

const {REACT_APP_API_URL,REACT_APP_RUN_INSIDE_BACKEND,REACT_APP_RELATIVE_API_URI} = process.env;
// const {SERVER_URL} = window;
console.log('REACT_APP_API_URL: ', REACT_APP_API_URL, ', REACT_APP_RUN_INSIDE_BACKEND: ', REACT_APP_RUN_INSIDE_BACKEND, ', REACT_APP_RELATIVE_API_URI: ', REACT_APP_RELATIVE_API_URI);

let API_URL;
if ( REACT_APP_RUN_INSIDE_BACKEND === 'true' ) {
  console.log('Use current location ', window.location.href);
  API_URL = window.location.href;
  if ( ! API_URL.endsWith('/') ) {
    API_URL += '/';
  }
  API_URL += REACT_APP_RELATIVE_API_URI;
} else {
  console.log('The application runs outside backend. Use ', REACT_APP_API_URL);
  API_URL = REACT_APP_API_URL;
}
console.log('Backend URL: ', API_URL);

const SongsState = (props) => {
  const INITIAL_STATE = {
    songs: [],
    loading: false,
    error: null,
  };

  const [state, dispatch] = useReducer(songsReducer, INITIAL_STATE);

  //fetch songs
  const fetchSongs = async () => {
    console.log("fetch songs...");

    setLoading();

    try {
      const res = await axios.get(`${API_URL}/v1/songs`);
      const data = res.data;
      console.log(data);

      dispatch({ type: FETCH_SONGS, payload: data.songs });

      return data;
    } catch (error) {
      console.error(error.response);
      setError(error.response ? error.response.data.msg : error.message);
    }
  };

  //rate song
  const rateSong = async (id, rating) => {
    console.log("rate song ", id, " rating ", rating);

    setLoading();

    try {
      const res = await axios.put(
        `${API_URL}/v1/songs/${id}`,
        { rating: rating },
        { headers: { "Content-Type": "application/json" } }
      );
      const data = res.data;
      console.log(data);

      dispatch({ type: RATE_SONG, payload: data });

      return data;
    } catch (error) {
      console.error(error.response);
      setError(error.response ? error.response.data.msg : error.message);
    }
  };

  //fetch songs
  const fetchMetrics = async () => {
    console.log("fetch metrics...");

    setLoading();

    try {
      const res = await axios.get(`${API_URL}/metrics`);
      const data = res.data;
      console.log(data);

      return data;
    } catch (error) {
      console.error(error.response);
      setError(error.response ? error.response.data.msg : error.message);
    }
  };

  //set loading
  const setLoading = () => {
    dispatch({ type: SET_LOADING });
  };

  //set error
  const setError = (error) => {
    dispatch({ type: SET_ERROR, payload: error });

    //clear error after 5 seconds
    setTimeout(() => clearError(), 5000);
  };

  //clear error
  const clearError = () => {
    dispatch({ type: CLEAR_ERROR });
  };

  return (
    <SongsContext.Provider
      value={{
        songs: state.songs,
        loading: state.loading,
        error: state.error,
        fetchSongs,
        rateSong,
        setError,
        fetchMetrics,
      }}
    >
      {props.children}
    </SongsContext.Provider>
  );
}

export default SongsState;
