import React, {useContext, useState} from 'react';

import SongsContext  from '../../context/songsContext';

const SongRatingTest = (props) => {
  const [params, setParams] = useState({id: 0, rating: 0});
  const [result, setResult] = useState(null);

  const {rateSong} = useContext(SongsContext);

  const handleChange = (e) =>{
    setParams({...params, [e.target.name]: e.target.value});
  }

  const handleTryIt = async (e) => {
    e.preventDefault();

    const data = await rateSong(params.id, params.rating);
    setResult(JSON.stringify(data));
  };

  const handleClear = async (e) => {
    setResult(null);
};

  return (
    <div className="container">
      <h3>Rate song</h3>
      <p>Endpoint: java_sample/v1/songs/:id</p>
      <p>Method: PUT</p>
      <p className="list">Parameters:</p>
      <ul>
        <li>Name: id, type: integer, required: true</li>
        <li>Name: rating, type: integer, required: true</li>
      </ul>
      <p>Result type: JSON</p>
      <form>
        <div className="form-group">
          <label htmlFor="id">Id</label>
          <input
            type="text"
            name="id"
            value={params.id}
            onChange={handleChange}
            placeholder="id"
          />
        </div>
        <div className="form-group">
          <label htmlFor="rating">Rating</label>
          <input
            type="text"
            name="rating"
            value={params.rating}
            onChange={handleChange}
            placeholder="rating"
          />
        </div>
        <div>
          <button
            type="submit"
            onClick={handleTryIt}
            className="btn btn-primary"
          >
            Try it
          </button>
          {result && (
            <button
              type="reset"
              onClick={handleClear}
              className="btn btn-light"
            >
              Clear
            </button>
          )}
        </div>
      </form>
      {result && (
        <div>
          <h4 className="py-1">Result</h4>
          <div>{result}</div>
        </div>
      )}
    </div>
  );
};

export default SongRatingTest;