import React, {useContext, useState} from 'react';

import SongsContext  from '../../context/songsContext';

const MetricsTest = (props) => {
    const [result, setResult] = useState(null);

    const {fetchMetrics} = useContext(SongsContext);

    const handleTryIt = async (e) => {
        const data = await fetchMetrics();
        setResult(data);
    };

    const handleClear = async (e) => {
        setResult(null);
    };

    // const songsStyle = {
    //     display: 'grid',
    //     gridTemplateColumns: 'repeat(2, 1fr)',
    //     gridGap: '20px'//'0.5rem'
    // };

    return (
      <div className="container">
        <div>
          <h3>Get songs metrics</h3>
          <p>Endpoint: java_sample/metrics</p>
          <p>Method: GET</p>
          {/* <p className="list">Parameters: -</p> */}
          <p>Result type: text</p>
          <div className="py-1">
            <button onClick={handleTryIt} className="btn btn-primary">
              Try it
            </button>
            {result && (
              <button onClick={handleClear} className="btn btn-light">
                Clear
              </button>
            )}
          </div>
        </div>
        {result && (
          <div>
            <h4 className="py-1">Result</h4>
            <div>{result}</div>
          </div>
        )}
      </div>
    );
};

export default MetricsTest;